 
import { useState, React } from 'react'; 
import TelegramWidget from './TelegramWidget';  
import Modal from './Modal';

const SocialLinks = () => {
    const links = [
      {
        name: "Telegram",
        url: "https://t.me/n3groserias", 
      },
      {
        name: "Twitter",
        url: "https://x.com/NegroseriasINC", 
      } 
    ];
  
    return (
      <div className='mb-4 '>
        {links.map((link) => (
          <a
            key={link.name}
            href={link.url}
            target="_blank"
            rel="noreferrer"
            className={`bg-gray-300 shadow hover:bg-gray-400   font-bold py-2 px-6 mr-3 rounded-full`}
          >
            {link.name}
          </a>
        ))}
      </div>
    );
  };

const Header = () => { 
    const [isModalOpen, setIsModalOpen] = useState(false); 
    const handleOpenModal = () => {
      setIsModalOpen(true);
    };
  
    const handleCloseModal = () => {
      setIsModalOpen(false);
    }; 
  return (  
<div> 
        <header class="p-2 grid grid-cols-1 sm:grid-cols-2 md:grid-cols-[2fr_2fr] items-center text-center flex items-center md:gap-4">   
          <div>  
            <h1 className=' text-wrap glitch md:text-8xl text-4xl'>-NEGRO<span className='text-red-700'>SERIAS</span> - </h1> 
          </div>  
          <a href="#" onClick={handleOpenModal} className='text-white rounded-full py-3 border bg-rose-500  hover:bg-rose-600 text-5xl glitch'>únete Aquí 👉</a>
          </header>   
          <Modal isOpen={isModalOpen} onClose={handleCloseModal}> 
          <div>
          <h2 className='mb-3 text-wrap glitch md:text-6xl text-4xl'>NEGRO<span className='text-red-700 '>SERIAS</span></h2> 
          <hr/>
           <div  className='py-5 md:text-2xl '>
            <p>Hola: Bienvenido</p>
            <p className='mb-2'>Hay dos formas para unirte a nuestro Equipo:</p>
            <p>👉Mediante la donación de alguna Crypto 💸</p>
            <p>👉unirte y Compartir nuestro Canal en  Telegram <a href='https://t.me/n3groserias' target='_blank' className='bg-slate-400 rounded-full px-2'>@n3groserias</a> 👌</p>
            <p className='mt-2'>Para ingresar a nuestro Chat Room Secreto es necesario <br/>que leas el post fijado en nuestro canal de telegram</p>
            <p className='bg-green-300 mt-4 rounded p-4'>Si realizaste tu donación escribenos a <strong>donaciones@negroserias.org</strong></p>
           </div>
            <button onClick={handleCloseModal} className="hover:bg-rose-500 bg-rose-200  py-4 w-full rounded">
                Cerrar  
            </button>
          </div>
        </Modal>
    </div>
  );
};

export default Header;
